// Margin & Padding klassert
$spaceamounts: (1,2,3,4,5);

@each $space in $spaceamounts{
    .m-#{$space}{
        margin: #{$space}rem;
    }
    .my-#{$space}{
        margin: #{$space}rem 0;
    }
    .p-#{$space}{
        Padding: #{$space}rem;
    }
    .py-#{$space}{
        Padding: #{$space}rem 0;
    }
}